.Navbar {
    width: 1920px;
    height: 82px;
    display: grid;
    grid-template-columns: 358px 793px calc(1920px - 358px - 793px);
    margin-left: 279px;
}
.contacts {
    margin-top: 14px;
    padding-right: 500px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: flex-end;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #000000;
}

.logo {
    margin-top: 10px;
    font-style: normal;
    font-weight: 600;
    font-size: 48px;
    line-height: 130%;
    color: #555555;
}
 .text {
    margin-top: 28px;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    color: #555555;
}







